import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

let lifeData = {};

try {
	// lifeDataAPP
	lifeData = uni.getStorageSync('lifeData');
} catch (e) {

}

// APPstate
let saveStateKeys = ['vuex_openid', 'vuex_token'];

// 
const saveLifeData = function(key, value) {
	// 
	if (saveStateKeys.indexOf(key) != -1) {
		// lifeData
		let tmp = uni.getStorageSync('lifeData');
		// APPlifeData{}
		tmp = tmp ? tmp : {};
		tmp[key] = value;
		// lifeData
		uni.setStorageSync('lifeData', tmp);
	}
}
const store = new Vuex.Store({
	state: {
		// lifeDatastate
		// vuex_
		vuex_loginStatus: false,
		vuex_openid: null,
		vuex_user: null,
		vuex_token: lifeData.vuex_token ? lifeData.vuex_token : '',
		// vuex_versionlifeData.vuex_version
		vuex_version: '1.0.1',
		vuex_demo: '',
		// tabbar
		vuex_tabbar: [{
				iconPath: "home",
				selectedIconPath: "home-fill",
				text: '',
				pagePath: '/pages/home/home' 
			}, 
			// {
			// 	iconPath: "list",
			// 	selectedIconPath: "list-dot", 
			// 	text: '',
			// 	pagePath: '/pages/todo/todo'
			// },
			{
				iconPath: "list",
				selectedIconPath: "list-dot", 
				text: '',
				pagePath: '/pages/settingTabs/settingTabs'
			},
			{
				iconPath: "/static/tabbar/pencil.png",
				selectedIconPath: "/static/tabbar/pencil-fill.png",
				text: '',
				midButton: true,
				pagePath: '/pages/writing/writing'
			}, {
				iconPath: "grid",
				selectedIconPath: "grid-fill",
				text: '',
				pagePath: '/pages/efficiency/efficiency'
			},
			{
				iconPath: "account",
				selectedIconPath: "account-fill",
				text: '',
				pagePath: '/pages/about/about'
			}
		]
	},
	mutations: {
		$uStore(state, payload) {
			// stateuser.info.score = 1
			let nameArr = payload.name.split('.');
			let saveKey = '';
			let len = nameArr.length;
			if (len >= 2) {
				let obj = state[nameArr[0]];
				for (let i = 1; i < len - 1; i++) {
					obj = obj[nameArr[i]];
				}
				obj[nameArr[len - 1]] = payload.value;
				saveKey = nameArr[0];
			} else {
				// state
				state[payload.name] = payload.value;
				saveKey = payload.name;
			}
			// 
			saveLifeData(saveKey, state[saveKey])
		}
	}
})

export default store
